import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { CrudService } from './crud.service';

@Injectable()
export class DriverService {

    public driversObs: Observable<any[]>;

    constructor(private crudService: CrudService) {
        this.driversObs = crudService.get('https://mobilidade-ufcg.herokuapp.com/driver').map((response) => {
        const drivers: any[] = [];
            for (const driverData of (<any>response).dataList) {
                drivers.push(driverData);
            }
            return drivers;
        });
    }

    create(model: Object ) {
        return this.crudService.post('https://mobilidade-ufcg.herokuapp.com/driver', model);
    }



    getById(id: number): Observable<any> {
        return this.crudService.get('https://mobilidade-ufcg.herokuapp.com/driver/' + id).map((response) => {
            let driver: any;
            driver = ((<any>response).data);
            return driver;
        });
    }

}



